<template>
	<view class="bg-white">
		<floor_title v-if="resdata.title" :title="resdata.title" :sub_title="resdata.sub_title" />
		
		<view class="h5">
			<rich-text :nodes="resdata.item_data|formatRichText"></rich-text>	
		</view>
	</view>
</template>

<script>
import lib from '@/common/common.js';
import floor_title from '@/components/template/floor_title.vue';
export default {
	components: {
		floor_title
	},
	props: {
		resdata: [Array, Object,String]
	},
	computed: {},
	methods: {
		mapUrl(item) {
			return lib.mapUrl(item);
		}
	},
	filters: {
		/**
		 * 处理富文本里的图片宽度自适应
		 * 1.去掉img标签里的style、width、height属性
		 * 2.img标签添加style属性：max-width:100%;height:auto
		 * 3.修改所有style里的width属性为max-width:100%
		 * 4.去掉<br/>标签
		 * @param html
		 * @returns {void|string|*}
		 */
		formatRichText (html) { //控制小程序中图片大小
			let newContent= html.replace(/<img[^>]*>/gi,function(match,capture){
				match = match.replace(/style="[^"]+"/gi, '').replace(/style='[^']+'/gi, '');
				match = match.replace(/width="[^"]+"/gi, '').replace(/width='[^']+'/gi, '');
				match = match.replace(/height="[^"]+"/gi, '').replace(/height='[^']+'/gi, '');
				return match;
			});
			newContent = newContent.replace(/style="[^"]+"/gi,function(match,capture){
				match = match.replace(/width:[^;]+;/gi, 'max-width:100%;').replace(/width:[^;]+;/gi, 'max-width:100%;');
				return match;
			});
			newContent = newContent.replace(/<br[^>]*\/>/gi, '');
			newContent = newContent.replace(/\<img/gi, '<img style="max-width:100%;height:auto;display:inline-block;margin:10upx auto;"');
			return newContent;
		}	
	}
};
</script>

<style>
	.h5{
		max-width: 750upx;
		width: 750upx;
		overflow-x: hidden!important;
		 *{
			max-width: 750upx;
		 }
	}
</style>
